.container{
    display: flex;
    margin-top: 35px;
}

.form{
    width: 730px;
    height: 69px;
    border-radius: 15px;
    box-shadow: 0px 16px 30px -10px rgba(70, 96, 187, 0.198567);
    display: flex;
    background: #FEFEFE;
}
.img{
    background-image: url(../../assets/icon-search.svg);
    width: 21px;
    height: 21px;
    color: var(--priamry-blue);
    margin-left: 32px;
    margin-top: 23px;
}
.input{
    width: 254px;
    height: 24px;
    margin-top: 23px;
    color: #4B6A9B;
    border: none;
    font-size: 18px;
    outline: none;
    margin-left: 24px;
}

.button{
    display: block;
    width: 106px;
    height: 50px;
    font-family: var(--space-mono);
    background: var(--primary-blue);
    border-radius: 10px;
    color: #fff;
    font-size: 16px;
    border: none;
    outline: none;
    margin-top: 9.5px;
    margin-left: auto ;
    margin-right: 9.5px;
}

.button:hover{
    cursor: pointer;
    background: #60ABFF;
    transition: all 0.3s ease;
}

.error{
    color: #f74646;
    font-weight: 700;
    font-size: 15px;
    line-height: 22px;
    font-family: var(--space-mono);
    margin-top: 22px;
    margin-left: 102px;
}

@media screen and (max-width: 376px) {
    .container{
        width: 100%;
        height: 100%;
        margin-right: 22px;
        display: flex;
        flex-direction: column;
    }
    .form{
        width: 627px;
        height: 68px;
    }
    .error{
        color: #f74646;
        font-weight: 700;
        font-size: 15px;
        line-height: 22px;
        font-family: var(--space-mono);
        margin-top: 22px;
        margin-left: 102px;
        width: 100px;
    }
}
